import React, { Component } from 'react';
import AddBkmkItem from './AddBkmkItem'

export default class BabeListItem extends Component {
    state = {editing: false}

    handleSave = (text, url) =>{
        const {babe, doUpdateBkmk } = this.props
        if (text.length>0 && url.length>0 ) {
            doUpdateBkmk(babe.id,  text, url)
        }
        this.setState({editing: false})
    }

    render() {
        const {babe,  doRemoveBkmk } = this.props

        let ele 
        if (this.state.editing) {
            ele=( <AddBkmkItem text={babe.text} url={babe.url} onSave={this.handleSave} />)
        }else {
            ele = (
                <div>
                    <a href={babe.url} target="_blank">{babe.text}</a>
                    <span className="btn btn_del" onClick={()=> doRemoveBkmk(babe.id)}>Delete</span>
                    <span className="btn btn_edit" onClick={()=> this.setState({editing: true})}>Edit</span>
                </div>
            )
        }
        return (
            <li>{ele}</li>
        );
    }
}



