import React, { Component } from 'react';

export default class AddBkbkItem extends Component {
    state = {
        text: this.props.text || '',
        url: this.props.url || ''
    }
    handleChangeName = e => {
        this.setState( { text: e.target.value.trim() })
    }
    handleChangeUrl = e => {
        this.setState( { url: e.target.value.trim() })
    }
    handleSubmit = () => {
        const text = this.refs.bk_name.value.trim()
        const url = this.refs.bk_url.value 
        this.props.onSave(text, url)
        if (this.props.newItem) {
            this.setState( { text: '', url:'' })
        }
    }

    render() {
        return (
            <div className="mod_add_bkmk">
                <input type="text" ref="bk_name" placeholder="title..." autoFocus="true" value={this.state.text} onChange={this.handleChangeName} />
                <input type="text" ref="bk_url" placeholder="url..." value={this.state.url} onChange={this.handleChangeUrl} />
                <span className="btn_save" onClick={this.handleSubmit} >Push</span>
            </div>
        );
    }
}



