import wilddog from 'wilddog'
//export const addBabe = (id,text) => ({type: 'ADD_babe',id, text})
//export const editBabe = (id, text) => ({type: 'EDIT_babe', id, text})
//export const deleteBabe = (id) => ({type: 'DELETE_babe', id})
//export const completeBabe = (id) => ({type: 'COMPLETE_babe', id })

export const changeCate = (cate) => ({type: 'CHANGE_cate', cate })

// init data conn
// Fetch, Push, Update , Remove
var config = { syncURL: "https://bkmk.wilddogio.com/"};
wilddog.initializeApp(config);
var ref = wilddog.sync().ref();

//Fetch
const fetchBabe = (cate) => ({ type: 'FETCH_babe', cate })
const fetchBabeOk = (cate, json) => ({ type: 'FETCH_babe_ok', cate, json })
export const doFetchBabe = (cate) => (dispatch) => {
    dispatch(fetchBabe(cate))
    ref.child(cate).on("value", function (data, error) {
        if (error == null) {
            dispatch(fetchBabeOk(cate, data.val()))
        }
    });
}

//Push
const pushBabe = (text) => ({ type: 'PUSH_babe', text })
export const doPushBabe = (cate, text) => (dispatch) => {
    dispatch(pushBabe(text))
    ref.child(cate).push({
        "name": text,
        "completed": false
    });
    //如果没有wilddog实时的数据监听，这里需要手动dispatch 操作成功的action(pushBabeOk)
    //.then(
    //dispatch(pushBabeOk(id, text))
    //)
}

//Remove
const removeBabe = (id) => ({ type: 'REMOVE_babe', id})
export const doRemoveBabe = (cate, id) => (dispatch) => {
    dispatch(removeBabe(id))
    ref.child(cate).child(id).remove();
}

//Update
const updateBabe = (id, key, value) => ({ type: 'UPDATE_babe', id, key, value})
export const doUpdateBabe = (id, key, value) => (dispatch) => {
    dispatch(updateBabe(id, key,value))
    let obj = {};
    obj[key] = value; 
    ref.child('todo').child(id).update(obj)
}

//BKMK==============
//fetch bkmk
//BKMK==============
const fetchBkmk = () => ({ type: 'FETCH_bkmk' })
const fetchBkmkOk = ( json) => ({ type: 'FETCH_bkmk_ok',  json })
export const doFetchBkmk = () => (dispatch) => {
    dispatch(fetchBkmk())
    ref.child('bookmark').on("value", function (data, error) {
        if (error == null) {
            dispatch(fetchBkmkOk(data.val()))
        }
    });
}
export const doPushBkmk = (text, url) => (dispatch) => {
    ref.child('bookmark').push({
        "name": text,
        "url":url
    });
}

//Remove
export const doRemoveBkmk = (id) => (dispatch) => {
    ref.child('bookmark').child(id).remove();
}

//Update
export const doUpdateBkmk = (id, text, url) => (dispatch) => {
    ref.child('bookmark').child(id).update({
        name: text,
        url: url
    })
}



