import React, { Component } from 'react';
import AddBkmkItem from './AddBkmkItem'
import BkmkListItem from './BkmkListItem'


export default class Bkmk extends Component {
    handleSave = (text,url) =>{
        this.props.actions.doPushBkmk(text ,url)
    }

    shouldComponentUpdate(nextProps){
        //todo ,should compare their value
        if (nextProps.bkmks=== this.props.bkmks) {
            return false ;
        }else {
            return true ;
        }
    }
    renderHeader() {
        return (
            <div className="section_hd section_hd_bk">
                <AddBkmkItem newItem onSave={this.handleSave} />
            </div>
        ) ;
    }

    render() {
        console.log('render ed', +new Date());
        const {bkmks, actions}  = this.props
        //部分命名可以抽象除了，比如传递给子组件的属性
        return (
            <section>
                {this.renderHeader()}
                <ul className="babe_list">
                    {bkmks.map(v => (<BkmkListItem key={v.id} babe={v} {...actions} />))}
                </ul>
            </section>
       );
    }
}



