import {combineReducers} from 'redux'

const initState = [{
    id: 0,
    text: 'loading',
    completed: false
}]

const babes = (state = initState, action) => {
    switch(action.type) {
        case 'ADD_babe':
            return [{
                    id: +new Date(),
                    text: action.text,
                    completed: false
                }, ...state] ;
        case 'EDIT_babe': 
            return state.map( item => item.id === action.id ? {...item, text:action.text} : item)
        case 'DELETE_babe': 
            return state.filter(item => item.id !== action.id) ;
        case 'DELETE_Complete_babe':
            return state.filter(item => !item.completed) ;
        case 'COMPLETE_babe': 
            return state.map(item => item.id === action.id ? {...item, completed: !item.completed} : item) ;
        case 'FETCH_babe':
            //add op status
            return state ;
        case 'FETCH_babe_ok':
            const D = action.json
            let arr = []
            for (var i in D) {
                if (D.hasOwnProperty(i)) {
                    arr.push({id: i, text: D[i]['name'], completed: D[i]['completed']})
                }
            }
            return arr ;

        default:
            return state ;
    }
}

const bkmks = (state = [{id:0, text:'LoadLink', url:''}], action) => {
    switch(action.type) {
        case 'FETCH_bkmk_ok':
            const D = action.json
            let arr = []
            for (var i in D) {
                if (D.hasOwnProperty(i)) {
                    arr.push({id: i, text: D[i]['name'], url: D[i]['url']})
                }
            }
            return arr ;
        default:
            return state ;
    }
}

const cate = (state = 'Babe', action) => {
    switch(action.type) {
        case 'CHANGE_cate':
            return action.cate ;
        default:
            return state ;
    }
}

export default combineReducers({
    babes,
    bkmks,
    cate
})



