import React, { Component } from 'react';

export default class AddBabeItem extends Component {
    state = {
        text: this.props.text || ''
    }
    handleChange = e => {
        this.setState( { text: e.target.value.trim() })
    }
    handleSubmit = e => {
        const text = e.target.value.trim ()
        if (e.which === 13) {
            this.props.onSave(text)
            if (this.props.newBabe) {
                this.setState( { text: '' })
            }
        }
    }
    handleBlur = (e) => {
        if (!this.props.newBabe) {
            this.props.onSave(e.target.value)
        }
    }

    render() {
        return (
            <input type="text" className="ipt_add" autoFocus="true" value={this.state.text}
                onChange={this.handleChange} onKeyUp={this.handleSubmit} onBlur={this.handleBlur} />
        );
    }
}



