import React, { Component } from 'react';
import AddBabeItem from './AddBabeItem'
import BabeListItem from './BabeListItem'


export default class Babe extends Component {
    handleSave = (text) =>{
        this.props.actions.doPushBabe('todo', text)
    }

    renderHeader() {
        return (
            <div className="section_hd">
                <AddBabeItem newBabe onSave={this.handleSave}/>
            </div>
        ) ;
    }

    render() {
        const {babes, actions}  = this.props
        return (
            <section>
                {this.renderHeader()}
                <ul className="babe_list">
                    {babes.map(v => <BabeListItem key={v.id} babe={v} {...actions}/>)}
                </ul>
            </section>
       );
    }
}



