import React, { Component } from 'react';

class Header extends Component {
    state = {active: 'Babe'}
    handleClick = e => {
        const actions = this.props.actions
        let cate = e.target.getAttribute('data-cate') ;
        if (!cate || cate === this.state.active) {
            return  ;
        }
        this.setState({active: cate})
        if (cate) {
            actions.changeCate(cate)
        }
    }
    render() {
        return (
            <div className="header" onClick={this.handleClick}>
                <p className={this.state.active==='Babe'? 'active':''} data-cate="Babe">Babes</p>
                <p className={this.state.active==='Bkmk'? 'active':''} data-cate="Bkmk">Bookmark</p>
            </div>
       );
    }
}

export default Header;



