import React, { Component } from 'react';

import Babe from './Babe'
import Bkmk from './Bkmk'

const dicData = {Babe: 'todo', Bkmk: 'bookmark'}

export default class MainSection extends Component {

    componentDidMount() {
        const {cate, actions} = this.props ;
        const cateDataName = dicData[cate]
        actions.doFetchBabe(cateDataName)
    }
    componentWillReceiveProps(nextProps) {
        if (nextProps.cate === this.props.cate) {
            return  ;
        }

        //todo 通用一个加载入口 根据参数判断
        const {cate, actions} = nextProps ;
        const cateDataName = dicData[cate]
        if (cateDataName === 'todo') {
            actions.doFetchBabe(cateDataName)
        }else {
            actions.doFetchBkmk(cateDataName)
        }
    }

    render() {
        const {babes, bkmks, cate, actions}  = this.props
        return (
            <div> 
            { cate === 'Babe' ? <Babe babes={babes} actions={actions} /> : <Bkmk bkmks={bkmks} actions={actions} /> }
            </div>
       );
    }
}



