import React, { Component } from 'react';
import AddBabeItem from './AddBabeItem'

export default class BabeListItem extends Component {
    state = {editing: false}

    handleSave = (text) =>{
        const {babe, doUpdateBabe } = this.props
        if (text.length>0 ) {
            doUpdateBabe(babe.id, 'name', text)
        }
        this.setState({editing: false})
    }

    render() {
        const {babe, doUpdateBabe, doRemoveBabe } = this.props

        let ele 
        if (this.state.editing) {
            ele=( <AddBabeItem text={babe.text} onSave={this.handleSave} />)
        }else {
            ele = (
                <div>
                    <input type="checkbox" checked={babe.completed} onChange={()=>doUpdateBabe(babe.id, 'completed', !babe.completed)} />
                    <span style={babe.completed? {color:'green'}: {color: '#333'}} onDoubleClick={()=>this.setState({editing: true})}>{babe.text}</span>
                    <span className="btn btn_del" onClick={()=> doRemoveBabe('todo', babe.id)}>Delete</span>
                    <span className="btn btn_edit" onClick={()=> this.setState({editing: true})}>Edit</span>
                </div>
            )
        }
        return (
            <li>{ele}</li>
        );
    }
}



